:root {
    --nav-bg: #4B4B4B;
    --color-logo: #EBCB80;
    --text-color: #4B4B4B;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

body {
    margin: 0;
}

ul,
ol {
    list-style-type: none;
}

p {
    margin: 0;
}

@font-face {
    font-family: Roboto, ;
    src: url(../assets/fonts/Roboto/Roboto-Regular.ttf);
}

.title--main {
    font-size: 44px;
    color: var(--text-color);
}

.sub-title {
    font-size: 16px;
    color: var(--text-color);
}